import React, { Component } from "react";

import { Button } from "./Button";

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      count: 1,
      todos: [
        { id: 1, name: "first" },
        { id: 2, name: "Second" },
        { id: 3, name: "Third" }
      ]
    };
  }

  handleCount(value) {
    this.setState((prevState) => ({ count: prevState.count + value }));
  }

  listItems() {
    return this.state.todos.map((todo, i) => (
      <li onClick={this.deleteTodo(todo.id)} key={i}>
        {todo.name}
      </li>
    ));
  }

  deleteTodo(id) {
    this.setState({
      todos: this.state.todos.filter((item) => item.id !== id)
    });
  }

  render() {
    return (
      <div>
        Current count: {this.state.count}
        {this.listItems()}
        <hr />
        <Button
          sign="+"
          count={this.state.count}
          updateCount={this.handleCount.bind(this)}
        />
        <Button
          sign="-"
          count={this.state.count}
          updateCount={this.handleCount.bind(this)}
        />
      </div>
    );
  }
}

export default App;
